import React, { useState, useEffect } from 'react';
//import { Link } from 'react-router-dom';


    /*Función utilizada para llamar el componente de autenticación
    */
   function autenticacion(intentos,device, tipoUser, id, name, patronymic, matronymic, ...args) {
    try{      
      window.Android.auth(parseInt(intentos),parseInt(device), parseInt(tipoUser), id, name, patronymic, matronymic, args);
    }catch(e){
      console.log(e);
    }
  }



const LoginComponent = (props) => {
  const [nEmpleado, setNEmpleado] = useState('')


  return (
    <div>
      <div>
        <input type="text"   onChange={e => setNEmpleado(e.target.value+'')} />
      </div>

      <div>
        <button className="btn2 btnVerde" onClick={()=>autenticacion(3,'6','1',nEmpleado, '', '', '', 2)}>Acceder con huella</button>
      </div>
    </div> 
  )
}

export default LoginComponent;